import {showToast,get} from '@/utils/index';
import {getBookByWordUrl} from '@/config';
import NovelBook from '@/components/novel-book/index.vue';
export default{
	data(){
		return{
			keyword: '',
			books: []
		}
	},
	components: {
		NovelBook
	},
	methods: {
		jumpPage(url){
			if(url && typeof(url)=='string'){
				uni.navigateTo({
					url: url,
					animationType: 'slide-in-bottom'
				});
			}
		},
		initData(){
			this.keyword = "";
			this.books = [];
		},
		startSearch(){
			if(!this.keyword){
				showToast('请输入搜索关键词');
				return;
			};
			this.getData();
		},
		getData(){
			get(getBookByWordUrl, {
				query: this.keyword
			})
			.then((res)=>{
				if(res.ok){
					let data = res.books;
					data.forEach((item)=>{
						item.cover = decodeURIComponent(item.cover).replace('/agent/', '');
					});
					this.books = data;
				}else{
					showToast('请求出错');
				}
			});
		}
	},
	onPullDownRefresh() {
		uni.stopPullDownRefresh();
		uni.hideNavigationBarLoading();
		this.initData();
		this.getData();
	}
}
